import axios from './axios';

// 获取所有历史记录
export const getUserDetectionRecords = () => {
  return axios.get('/history');
};

// 根据类型获取历史记录
export const getDetectionRecordsByType = (typeId) => {
  return axios.get(`/history/type/${typeId}`);
};

// 根据时间范围获取历史记录
export const getDetectionRecordsByTimeRange = (timeRange) => {
  return axios.get('/history/time', { params: { timeRange } });
};

// 根据检测结果获取历史记录
export const getDetectionRecordsByResult = (result) => {
  return axios.get(`/history/result/${result}`);
};

// 获取历史记录详情
export const getDetectionRecordDetail = (id) => {
  return axios.get(`/history/${id}`);
};

// 获取所有检测记录
export const getDetectionRecords = (params) => {
  return axios.get('/history/records', { params });
};

// 获取检测记录详情
export const getDetectionDetail = (recordId) => {
  return axios.get(`/history/records/${recordId}`);
};

// 删除检测记录
export const deleteDetectionRecord = (recordId) => {
  return axios.delete(`/history/records/${recordId}`);
};

// 消耗用户额度，进行内容检测
export const detectContent = (data) => {
  console.log('调用检测内容API，消耗用户额度:', data);
  return axios.post('/detection/detect', data, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
}; 